"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { Slider } from "@/components/ui/slider";
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from "@/components/ui/accordion";

interface Property {
  id: string;
  title: string;
  price: number;
  location: string;
  area: string;
  rooms: number;
  bathrooms: number;
  size: number;
  floor: string;
  type: string;
  features: string[];
  images: string[];
  isNew: boolean;
  isTemporal: boolean;
  description: string;
  coordinates: { lat: number; lng: number };
}

interface Filters {
  priceRange: [number, number];
  propertyTypes: string[];
  rooms: string;
  sizeMin: string;
  sizeMax: string;
  features: string[];
  rentalType: string[];
  location: string;
}

export default function PropiedadesPage() {
  const [properties, setProperties] = useState<Property[]>([]);
  const [filteredProperties, setFilteredProperties] = useState<Property[]>([]);
  const [filters, setFilters] = useState<Filters>({
    priceRange: [0, 5000],
    propertyTypes: [],
    rooms: '',
    sizeMin: '',
    sizeMax: '',
    features: [],
    rentalType: [],
    location: ''
  });
  const [sortBy, setSortBy] = useState('relevancia');
  const [viewMode, setViewMode] = useState<'grid' | 'list'>('grid');
  const [showMap, setShowMap] = useState(true);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(true);

  // Mock data
  const mockProperties: Property[] = [
    {
      id: '1',
      title: 'Piso moderno en el centro de Madrid',
      price: 1200,
      location: 'Centro, Madrid',
      area: 'Sol',
      rooms: 2,
      bathrooms: 1,
      size: 75,
      floor: '3º',
      type: 'Piso',
      features: ['Ascensor', 'Calefacción', 'Aire acondicionado'],
      images: ['/placeholder-property-1.jpg'],
      isNew: true,
      isTemporal: false,
      description: 'Hermoso piso completamente reformado en pleno centro',
      coordinates: { lat: 40.4168, lng: -3.7038 }
    },
    {
      id: '2',
      title: 'Casa con jardín en Salamanca',
      price: 2500,
      location: 'Salamanca, Madrid',
      area: 'Recoletos',
      rooms: 4,
      bathrooms: 3,
      size: 150,
      floor: 'Bajo',
      type: 'Casa',
      features: ['Jardín', 'Garaje', 'Piscina', 'Amueblado'],
      images: ['/placeholder-property-2.jpg'],
      isNew: false,
      isTemporal: true,
      description: 'Espectacular casa unifamiliar con todas las comodidades',
      coordinates: { lat: 40.4300, lng: -3.6900 }
    },
    {
      id: '3',
      title: 'Ático con terraza en Malasaña',
      price: 1800,
      location: 'Malasaña, Madrid',
      area: 'Universidad',
      rooms: 3,
      bathrooms: 2,
      size: 95,
      floor: '5º',
      type: 'Ático',
      features: ['Terraza', 'Ascensor', 'Mascotas permitidas'],
      images: ['/placeholder-property-3.jpg'],
      isNew: true,
      isTemporal: false,
      description: 'Ático luminoso con gran terraza y vistas panorámicas',
      coordinates: { lat: 40.4270, lng: -3.7000 }
    },
    {
      id: '4',
      title: 'Estudio para estudiantes en Chamberí',
      price: 800,
      location: 'Chamberí, Madrid',
      area: 'Alonso Martínez',
      rooms: 1,
      bathrooms: 1,
      size: 35,
      floor: '2º',
      type: 'Estudio',
      features: ['Amueblado', 'Internet incluido'],
      images: ['/placeholder-property-4.jpg'],
      isNew: false,
      isTemporal: true,
      description: 'Estudio perfecto para estudiantes, completamente equipado',
      coordinates: { lat: 40.4350, lng: -3.6950 }
    },
    {
      id: '5',
      title: 'Dúplex con garaje en Retiro',
      price: 2200,
      location: 'Retiro, Madrid',
      area: 'Ibiza',
      rooms: 3,
      bathrooms: 2,
      size: 110,
      floor: '1º-2º',
      type: 'Dúplex',
      features: ['Garaje', 'Trastero', 'Calefacción central'],
      images: ['/placeholder-property-5.jpg'],
      isNew: false,
      isTemporal: false,
      description: 'Dúplex en zona tranquila con todas las comodidades',
      coordinates: { lat: 40.4100, lng: -3.6800 }
    },
    {
      id: '6',
      title: 'Piso con piscina en Chamartín',
      price: 1600,
      location: 'Chamartín, Madrid',
      area: 'Prosperidad',
      rooms: 3,
      bathrooms: 2,
      size: 85,
      floor: '4º',
      type: 'Piso',
      features: ['Piscina', 'Ascensor', 'Aire acondicionado', 'Garaje'],
      images: ['/placeholder-property-6.jpg'],
      isNew: true,
      isTemporal: false,
      description: 'Piso en urbanización con piscina y zonas comunes',
      coordinates: { lat: 40.4500, lng: -3.6700 }
    }
  ];

  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setProperties(mockProperties);
      setFilteredProperties(mockProperties);
      setIsLoading(false);
    }, 1000);
  }, []);

  useEffect(() => {
    applyFilters();
  }, [filters, properties, sortBy]);

  const applyFilters = () => {
    let filtered = [...properties];

    // Price filter
    filtered = filtered.filter(p => 
      p.price >= filters.priceRange[0] && p.price <= filters.priceRange[1]
    );

    // Property type filter
    if (filters.propertyTypes.length > 0) {
      filtered = filtered.filter(p => filters.propertyTypes.includes(p.type));
    }

    // Rooms filter
    if (filters.rooms && filters.rooms !== 'all') {
      if (filters.rooms === '5+') {
        filtered = filtered.filter(p => p.rooms >= 5);
      } else {
        filtered = filtered.filter(p => p.rooms === parseInt(filters.rooms));
      }
    }

    // Size filter
    if (filters.sizeMin) {
      filtered = filtered.filter(p => p.size >= parseInt(filters.sizeMin));
    }
    if (filters.sizeMax) {
      filtered = filtered.filter(p => p.size <= parseInt(filters.sizeMax));
    }

    // Features filter
    if (filters.features.length > 0) {
      filtered = filtered.filter(p => 
        filters.features.some(feature => p.features.includes(feature))
      );
    }

    // Rental type filter
    if (filters.rentalType.length > 0) {
      filtered = filtered.filter(p => {
        if (filters.rentalType.includes('temporal') && p.isTemporal) return true;
        if (filters.rentalType.includes('larga-temporada') && !p.isTemporal) return true;
        return false;
      });
    }

    // Location filter
    if (filters.location) {
      filtered = filtered.filter(p => 
        p.location.toLowerCase().includes(filters.location.toLowerCase()) ||
        p.area.toLowerCase().includes(filters.location.toLowerCase())
      );
    }

    // Sort
    switch (sortBy) {
      case 'precio-asc':
        filtered.sort((a, b) => a.price - b.price);
        break;
      case 'precio-desc':
        filtered.sort((a, b) => b.price - a.price);
        break;
      case 'mas-recientes':
        filtered.sort((a, b) => (b.isNew ? 1 : 0) - (a.isNew ? 1 : 0));
        break;
      default:
        // Relevancia - keep original order
        break;
    }

    setFilteredProperties(filtered);
  };

  const handleFilterChange = (key: keyof Filters, value: any) => {
    setFilters(prev => ({ ...prev, [key]: value }));
  };

  const toggleFeature = (feature: string) => {
    setFilters(prev => ({
      ...prev,
      features: prev.features.includes(feature)
        ? prev.features.filter(f => f !== feature)
        : [...prev.features, feature]
    }));
  };

  const togglePropertyType = (type: string) => {
    setFilters(prev => ({
      ...prev,
      propertyTypes: prev.propertyTypes.includes(type)
        ? prev.propertyTypes.filter(t => t !== type)
        : [...prev.propertyTypes, type]
    }));
  };

  const toggleRentalType = (type: string) => {
    setFilters(prev => ({
      ...prev,
      rentalType: prev.rentalType.includes(type)
        ? prev.rentalType.filter(t => t !== type)
        : [...prev.rentalType, type]
    }));
  };

  return (
    <div className="min-h-screen bg-gray-50 pt-16">
      {/* Top Menu Bar */}
      <div className="bg-white border-b border-gray-200 sticky top-16 z-40">
        <div className="max-w-7xl mx-auto px-4 py-3">
          <div className="flex items-center justify-between">
            <nav className="flex space-x-6">
              <Button variant="ghost" className="text-primary font-semibold">Alquilar</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Comprar</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Alquiler Vacacional</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Proyectos Nuevos</Button>
              <Button variant="ghost" className="text-gray-600 hover:text-primary">Locales</Button>
            </nav>
            <div className="flex items-center space-x-4">
              <Button className="bg-primary hover:bg-primary/90 text-secondary">
                Publicar anuncio
              </Button>
              <Button variant="outline">Iniciar sesión</Button>
              <Button variant="ghost" size="sm">🌐 ES</Button>
              <Button variant="ghost" size="sm">❓</Button>
              <Button variant="ghost" size="sm">🔔</Button>
            </div>
          </div>
        </div>
      </div>

      {/* Search Bar */}
      <div className="bg-white border-b border-gray-200 py-4">
        <div className="max-w-7xl mx-auto px-4">
          <div className="flex items-center space-x-4">
            <div className="flex-1 relative">
              <Input
                placeholder="Buscar por ciudad, barrio o estación..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-4 pr-12 py-3 text-lg"
              />
              <Button 
                size="sm" 
                className="absolute right-2 top-1/2 transform -translate-y-1/2 bg-primary hover:bg-primary/90"
              >
                🔍
              </Button>
            </div>
            <Button 
              variant="outline" 
              onClick={() => setShowMap(!showMap)}
              className={showMap ? 'bg-primary text-secondary' : ''}
            >
              {showMap ? 'Ocultar mapa' : 'Ver mapa'}
            </Button>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 py-6">
        <div className="flex gap-6">
          {/* Left Sidebar - Filters */}
          <div className="w-80 flex-shrink-0">
            <Card className="sticky top-32">
              <CardHeader>
                <CardTitle className="text-lg">Filtros</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <Accordion type="multiple" defaultValue={["precio", "tipo", "habitaciones"]}>
                  {/* Price Range */}
                  <AccordionItem value="precio">
                    <AccordionTrigger>Rango de Precio</AccordionTrigger>
                    <AccordionContent className="space-y-4">
                      <div className="px-2">
                        <Slider
                          value={filters.priceRange}
                          onValueChange={(value) => handleFilterChange('priceRange', value)}
                          max={5000}
                          min={0}
                          step={50}
                          className="w-full"
                        />
                        <div className="flex justify-between text-sm text-gray-600 mt-2">
                          <span>€{filters.priceRange[0]}</span>
                          <span>€{filters.priceRange[1]}</span>
                        </div>
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Property Type */}
                  <AccordionItem value="tipo">
                    <AccordionTrigger>Tipo de Propiedad</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      {['Piso', 'Casa', 'Ático', 'Estudio', 'Dúplex'].map(type => (
                        <div key={type} className="flex items-center space-x-2">
                          <Checkbox
                            id={type}
                            checked={filters.propertyTypes.includes(type)}
                            onCheckedChange={() => togglePropertyType(type)}
                          />
                          <Label htmlFor={type} className="text-sm">{type}</Label>
                        </div>
                      ))}
                    </AccordionContent>
                  </AccordionItem>

                  {/* Rooms */}
                  <AccordionItem value="habitaciones">
                    <AccordionTrigger>Habitaciones</AccordionTrigger>
                    <AccordionContent>
                      <Select value={filters.rooms} onValueChange={(value) => handleFilterChange('rooms', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Cualquiera" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="all">Cualquiera</SelectItem>
                          <SelectItem value="1">1 habitación</SelectItem>
                          <SelectItem value="2">2 habitaciones</SelectItem>
                          <SelectItem value="3">3 habitaciones</SelectItem>
                          <SelectItem value="4">4 habitaciones</SelectItem>
                          <SelectItem value="5+">5 o más</SelectItem>
                        </SelectContent>
                      </Select>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Size */}
                  <AccordionItem value="metros">
                    <AccordionTrigger>Metros Cuadrados</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      <div className="grid grid-cols-2 gap-2">
                        <div>
                          <Label className="text-xs">Mínimo</Label>
                          <Input
                            placeholder="Min m²"
                            value={filters.sizeMin}
                            onChange={(e) => handleFilterChange('sizeMin', e.target.value)}
                            type="number"
                          />
                        </div>
                        <div>
                          <Label className="text-xs">Máximo</Label>
                          <Input
                            placeholder="Max m²"
                            value={filters.sizeMax}
                            onChange={(e) => handleFilterChange('sizeMax', e.target.value)}
                            type="number"
                          />
                        </div>
                      </div>
                    </AccordionContent>
                  </AccordionItem>

                  {/* Features */}
                  <AccordionItem value="caracteristicas">
                    <AccordionTrigger>Características</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      {['Ascensor', 'Piscina', 'Jardín', 'Amueblado', 'Mascotas permitidas', 'Garaje', 'Terraza', 'Aire acondicionado'].map(feature => (
                        <div key={feature} className="flex items-center space-x-2">
                          <Checkbox
                            id={feature}
                            checked={filters.features.includes(feature)}
                            onCheckedChange={() => toggleFeature(feature)}
                          />
                          <Label htmlFor={feature} className="text-sm">{feature}</Label>
                        </div>
                      ))}
                    </AccordionContent>
                  </AccordionItem>

                  {/* Rental Type */}
                  <AccordionItem value="alquiler">
                    <AccordionTrigger>Tipo de Alquiler</AccordionTrigger>
                    <AccordionContent className="space-y-3">
                      <div className="flex items-center space-x-2">
                        <Checkbox
                          id="temporal"
                          checked={filters.rentalType.includes('temporal')}
                          onCheckedChange={() => toggleRentalType('temporal')}
                        />
                        <Label htmlFor="temporal" className="text-sm">Temporal</Label>
                      </div>
                      <div className="flex items-center space-x-2">
                        <Checkbox
                          id="larga-temporada"
                          checked={filters.rentalType.includes('larga-temporada')}
                          onCheckedChange={() => toggleRentalType('larga-temporada')}
                        />
                        <Label htmlFor="larga-temporada" className="text-sm">Larga temporada</Label>
                      </div>
                    </AccordionContent>
                  </AccordionItem>
                </Accordion>

                <Button 
                  variant="outline" 
                  className="w-full"
                  onClick={() => setFilters({
                    priceRange: [0, 5000],
                    propertyTypes: [],
                    rooms: '',
                    sizeMin: '',
                    sizeMax: '',
                    features: [],
                    rentalType: [],
                    location: ''
                  })}
                >
                  Limpiar filtros
                </Button>
              </CardContent>
            </Card>
          </div>

          {/* Main Content */}
          <div className="flex-1">
            {/* Results Bar */}
            <div className="bg-white rounded-lg border border-gray-200 p-4 mb-6">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <span className="text-lg font-semibold">
                    {filteredProperties.length} propiedades encontradas
                  </span>
                  <Button variant="ghost" size="sm" className="text-primary">
                    ❤️ Guardar búsqueda
                  </Button>
                  <Button variant="ghost" size="sm" className="text-primary">
                    📤 Compartir
                  </Button>
                </div>
                <div className="flex items-center space-x-4">
                  <Select value={sortBy} onValueChange={setSortBy}>
                    <SelectTrigger className="w-48">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="relevancia">Relevancia</SelectItem>
                      <SelectItem value="precio-asc">Precio (menor a mayor)</SelectItem>
                      <SelectItem value="precio-desc">Precio (mayor a menor)</SelectItem>
                      <SelectItem value="mas-recientes">Más recientes</SelectItem>
                    </SelectContent>
                  </Select>
                  <div className="flex border rounded-lg">
                    <Button
                      variant={viewMode === 'grid' ? 'default' : 'ghost'}
                      size="sm"
                      onClick={() => setViewMode('grid')}
                      className="rounded-r-none"
                    >
                      ⊞
                    </Button>
                    <Button
                      variant={viewMode === 'list' ? 'default' : 'ghost'}
                      size="sm"
                      onClick={() => setViewMode('list')}
                      className="rounded-l-none"
                    >
                      ☰
                    </Button>
                  </div>
                </div>
              </div>
            </div>

            <div className="flex gap-6">
              {/* Properties List */}
              <div className={showMap ? 'flex-1' : 'w-full'}>
                {isLoading ? (
                  <div className="text-center py-12">
                    <div className="animate-spin w-12 h-12 border-4 border-primary border-t-transparent rounded-full mx-auto mb-4"></div>
                    <p className="text-gray-600">Cargando propiedades...</p>
                  </div>
                ) : (
                  <div className={viewMode === 'grid' ? 'grid grid-cols-1 md:grid-cols-2 gap-6' : 'space-y-4'}>
                    {filteredProperties.map((property) => (
                      <Card key={property.id} className="overflow-hidden hover:shadow-lg transition-shadow cursor-pointer">
                        <div className="relative">
                          <div className="aspect-video bg-gray-200 flex items-center justify-center">
                            <span className="text-gray-500">Imagen de propiedad</span>
                          </div>
                          {property.isNew && (
                            <Badge className="absolute top-2 left-2 bg-green-500">Nuevo hoy</Badge>
                          )}
                          {property.isTemporal && (
                            <Badge className="absolute top-2 right-2 bg-blue-500">Alquiler temporal</Badge>
                          )}
                        </div>
                        <CardContent className="p-4">
                          <div className="flex justify-between items-start mb-2">
                            <h3 className="font-semibold text-lg line-clamp-2">{property.title}</h3>
                            <span className="text-2xl font-bold text-primary">€{property.price}</span>
                          </div>
                          <p className="text-gray-600 mb-2">{property.location}</p>
                          <div className="flex items-center space-x-4 text-sm text-gray-500 mb-3">
                            <span>{property.rooms} hab.</span>
                            <span>{property.bathrooms} baños</span>
                            <span>{property.size} m²</span>
                            <span>{property.floor}</span>
                          </div>
                          <div className="flex flex-wrap gap-1 mb-3">
                            {property.features.slice(0, 3).map((feature, idx) => (
                              <Badge key={idx} variant="secondary" className="text-xs">
                                {feature}
                              </Badge>
                            ))}
                            {property.features.length > 3 && (
                              <Badge variant="secondary" className="text-xs">
                                +{property.features.length - 3} más
                              </Badge>
                            )}
                          </div>
                          <div className="flex justify-between items-center">
                            <Button variant="outline" size="sm">Ver detalles</Button>
                            <div className="flex space-x-2">
                              <Button variant="ghost" size="sm">❤️</Button>
                              <Button variant="ghost" size="sm">📤</Button>
                            </div>
                          </div>
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                )}

                {/* Pagination */}
                {!isLoading && filteredProperties.length > 0 && (
                  <div className="flex justify-center mt-8">
                    <div className="flex space-x-2">
                      <Button variant="outline" disabled>Anterior</Button>
                      <Button variant="default">1</Button>
                      <Button variant="outline">2</Button>
                      <Button variant="outline">3</Button>
                      <Button variant="outline">Siguiente</Button>
                    </div>
                  </div>
                )}
              </div>

              {/* Map */}
              {showMap && (
                <div className="w-96 flex-shrink-0">
                  <Card className="sticky top-32 h-96">
                    <CardContent className="p-4 h-full">
                      <div className="bg-gray-100 rounded-lg h-full flex items-center justify-center">
                        <div className="text-center">
                          <div className="text-4xl mb-2">🗺️</div>
                          <p className="text-gray-600">Mapa Interactivo</p>
                          <p className="text-sm text-gray-500 mt-2">
                            {filteredProperties.length} propiedades en el mapa
                          </p>
                          <Button variant="outline" size="sm" className="mt-3">
                            Dibujar área
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
